<template>
  <div class="currentCareDetail">
    <mt-header class="header">
      <a slot="left"  @click="back">
        <mt-button icon="back">护工详情</mt-button>
      </a>
    </mt-header>
    <div class="currentCareDetailBody">
      <div class="currentCareDetailBodyHeader">
        <img class="bg" src="../assets/my_background.png" alt="">
        <img class="avator" :src="currentCare.avatar" alt="">
        <div class="info">
          <p style="margin-bottom:0.2rem;">{{currentCare.name}}</p>
          <p style="min-width:1.5rem;"><span>工号</span><span>{{currentCare.id}}</span></p> 
        </div>
      </div>
      <div class="currentCareDetailBodyInfo">
        <div>
          <p>护理时长</p>
          <p class="d" style="margin-top:0.2rem;">{{currentCare.workingTime}}小时</p>
        </div>
        <div>
          <p>接单数</p>
          <p class="d">{{currentCare.orderCount}}单</p>
        </div>
        <div>
          <p>等级</p>
          <p class="d">{{currentCare.level}}</p>
        </div>
      </div>
      <div class="currentCareDetailBodyMain">
         <mt-cell title="民族" :value="currentCare.nation"></mt-cell>
         <mt-cell title="性别" :value="currentCare.sex"></mt-cell>
         <mt-cell title="年龄" :value="currentCare.age"></mt-cell>
         <mt-cell title="籍贯" :value="currentCare.nativePlace"></mt-cell>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import {careChange} from '../utils/util';
export default {
  name: 'currentCareDetail',
  data () {
    return {
      avatar: '/static/defaultAvatar.png',
      data: '12',
      currentCare:{
        avatar: '/static/defaultAvatar.png'
      },
      code: ''
    }
  },
  methods: {
    ...mapActions({
      getCurrentCare: 'getCurrentCare'
    }),
    back: function() {
      history.go(-1);
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.code = to.query.code;
      vm.getCurrentCare(to.query.code).then(response => {
        vm.currentCare = careChange(response.data);
      })
    })
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.currentCareDetailBodyHeader {
  position: relative;
  width: 100%;
  height: 30%;
}
.currentCareDetailBody .bg {
  height: 5rem;
  width: 100%;
}
.currentCareDetailBody img.avator {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 1rem;
  width: 2rem;
  height: 2rem;
  border-radius: 1rem;
  z-index: 0;
}
.currentCareDetailBody .info {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 3.5rem;
  width: auto;
  height: 1.2rem;
  z-index: 0;
  color: #ffffff;
  font-size: 0.5rem;
}
.currentCareDetailBodyInfo {
  display: flex;
  font-size: 0.4rem;
  padding: 0.2rem 0.4rem;
  justify-content: space-between;
}
.currentCareDetailBodyInfo div {
  width: 3rem;
  flex: 1 1 auto;
  background: url(../assets/line.png) no-repeat;
  background-position: right;
}
.currentCareDetailBodyInfo div p{
  text-align: center;
  font-size: 0.3rem;
  color: #565256;
}
.currentCareDetailBodyInfo div .d {
  margin-top: 0.3rem;
  font-size: 0.4rem;
  color: #565256;
}
.currentCareDetailBodyMain {
  padding: 0 1rem
}
.currentCareDetailBodyMain .mint-cell-text {
  color: #565256;
}
.currentCareDetailBodyMain .mint-cell-value {
  color: #565256;
  min-width: 1rem;
}
</style>



